export function onModalClose(event) {
    window.removeEventListener('keydown', onEscKeyPress);
    modalEl.classList.remove('is-open');
};

export default function onEscKeyPress(event) {
    if (event.code === 'Escape') {
        onModalClose(event)
    }
};

